import React from 'react';

const MangaPreview: React.FC = () => {
    return (
        <figure className="manga-preview-container">
            <figcaption className="manga-preview-info">
                <div id="manga-preview-title">Title</div>
                <div className="flex-container">
                    <div id="manga-preview-latest-chapter">Chapter</div>
                    <div id="manga-preview-latest-update">Latest update</div>
                </div>
            </figcaption>
        </figure>
    );
}

export default MangaPreview